.container{
    display: flex;
    justify-content: center;
    margin-top: 50px;
}

td, th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
  }
  
  tr:nth-child(even) {
    background-color: #dddddd;
  }

  .clickable{
      background-color: burlywood;
      cursor: pointer;
  }

  .delete{
      background-color: crimson;
      cursor: pointer;
      color: white
  }